.services {
  padding-top: 3vh;
  margin: 2vh;
}

.services > h1 {
  margin-bottom: 1vh;
}

.services .container{
  margin: auto;
  padding: 3vh 3vh 5vh;
} 
.services .container *{
    font-family: “Poppins”,sans-serif;
    color: black;
    font-weight: 500;
}

.services > .container > h2{
  text-align: center;
  font-size: 3vh;
  font-weight: 500;
}

.servicesList {
  display: flex;
  align-items: center;
  justify-content: center;
}

.servicesList > ul {
  list-style: none;
  padding-left: 0;
  text-align: start;
}

.servicesList > ul > li > span {
  font-size: 20px;
  padding-right: 2vw;
}